.mhhc-label {
    display: block;
    font-weight: 500;
    color: #1A2C37;
    margin-bottom






    : 6px;
}

/* Info-tekst og fejl-tekst i hver sin boks */
.mhhc-help-text{
    margin-top: 8px;
    font-size: 13px;
    color: #0E1920;
    background:#f7fbff;
    border-left: 3px solid #91d5ff;
    padding: 10px 12px;
    border-radius: 4px;
}

.mhhc-field-error {
    margin-top: 8px;
    color: #B4190F;
    background: #FEF3F2;
    border-left: 3px solid #FCA5A5;
    padding: 10px 12px;
    border-radius: 4px;
    font-size: 13px;
}

/* Select/input fuld bredde inde i .mhhc-input-control */
.mhhc-input-control select,
.mhhc-input-control input,
.mhhc-input-control textarea {
    width: 100%;
}
